import React from 'react';
import CollegeCards from '../CollegeCards';

function Mpharma() {
  return (
    <>
        <div className='btechClgs'>
            <CollegeCards name="Dr. D. Y. Patil Institute of Pharmaceutical Sciences and Research" bio="Dr. D. Y. Patil Institute of Pharmaceutical Sciences and Research, Pimpri, Pune was established with the aim to impart quality pharmacy education and training to cater the needs of the pharmacy profession and society at large. The journey started in 1994 by introducing the first Pharmacy institute in the Pimpri Chinchwad area of Pune with B. Pharm course. The visionary leadership of Hon. Dr. P. D. Patil and dynamism of Dr. Somnath Patil has steered it to its present iconic status; offering Diploma Programme (D. Pharm.), undergraduate program B. Pharm and Pharm. D. to both national and international students, post-graduate program (M. Pharm) in Pharmaceutics, Pharmacology and Pharmaceutical Quality Assurance and doctoral (Ph. D.) program. " linked="https://pharmacy.dypvp.edu.in/" />
            <CollegeCards name="Rajgad Dnyanpeeth's College of Pharmacy" bio="One of the many unaided colleges of Pharmacy in Maharashtra, Rajghad Dnyeenpeeth's College of Pharmacy is located in the town of Bhor in Pune district. It is managed by Rajghad Dnyeenpeeth foundation which was established by Anantrao Thopte." linked="http://www.rdcopbhor.com/" /><CollegeCards name="POONA COLLEGE OF PHARMACY" bio="Poona College of Pharmacy was earlier permanently affiliated to University of Pune. It is now a constituent unit of Bharati Vidyapeeth (Deemed to be University). It is a grant-in-aid institute, conducting Graduate, Post-graduate and Doctoral programmes in Pharmaceutical Sciences." linked="http://pcp.bharatividyapeeth.edu/" />
            <CollegeCards name="Sri Ramachandra Institute of Higher Education and Research" bio="Sri Ramachandra Institute of Higher Education and Research, formerly Sri Ramachandra University, formerly Sri Ramachandra Medical College and Research Institute, is a private institute located in Porur, Chennai, India." linked="http://www.sriramachandra.edu.in/ " />
            <CollegeCards name="Maharshi Dayanand University" bio="Maharshi Dayanand University, also called M.D. University or simply MDU, is a university in Rohtak, Haryana, India. Established in 1976 and named after the saint Dayananda Saraswati ." linked="https://mdu.ac.in/" />
            
        </div>
    </>
  )
}

export default Mpharma