import React from 'react'
import CollegeCards from '../CollegeCards';

function Mtech() {
  return (
    <>
        <div className='btechClgs'>
        <CollegeCards name ="NIT KURUKSHETRA" bio="National Institute of Technology Kurukshetra is a public technical and research university located in Kurukshetra. In December 2008, it was accredited with the status of Institute of National Importance. It is one of the 30 National Institutes of Technology established and administered by Government of India." linked="https://nitkkr.ac.in/" />
        <CollegeCards name ="VIT VELLORE" bio="Vellore Institute of Technology is a private research deemed university located in Katpadi in Vellore, India. Founded in 1984 as Vellore Engineering College by G. Viswanathan, the institution offers 66 Undergraduate, 58 Postgraduate, 15 Integrated, 2 Research and 2 M.Tech Industrial Programmes." linked="https://vit.ac.in/" />
        <CollegeCards name ="IIT MADRAS" bio="Indian Institute of Technology Madras is a public technical university located in Chennai, Tamil Nadu, India. As one of the Indian Institutes of Technology, it is recognized as an Institute of National Importance and has been consistently rated as one of India's most prestigious universities." linked="http://www.iitm.ac.in/" />
        <CollegeCards name ="BIT MESRA" bio="Birla Institute of Technology, Mesra is a public deemed institute in Jharkhand, India. It was established in 1955 at Mesra, Ranchi, by the industrialist B. M. Birla. The institute was later headed by G. P. Birla, and the present chairman of the board of governors is C. K. Birla" linked="https://www.bitmesra.ac.in/" />
        <CollegeCards name ="BITS PILANI" bio="Birla Institute of Technology & Science, Pilani is a Deemed university in Pilani, Jhunjhunu, India. It focuses primarily on higher education and research in engineering and sciences." linked="http://www.bits-pilani.ac.in/" />
        <CollegeCards name ="NIT DELHI" bio="National Institute of Technology, Delhi is a premier public technical university located in Delhi, India. It has been declared as an Institute of National Importance by an act of Parliament of India. It is one of the 31 National Institutes of Technology in India." linked="http://www.nitdelhi.ac.in/" />
        <CollegeCards name ="IIT BOMBAY" bio="The Indian Institute of Technology Bombay is a public research university and technical institute in Powai, Mumbai, Maharashtra, India. IIT Bombay was founded in 1958. IITB has grown from strength to strength to emerge as one of the top technical universities in the world. The institute is recognised worldwide as a leader in the field of engineering education and research." linked="https://www.iitb.ac.in/" />
        <CollegeCards name ="IIT ROORKEE" bio="Indian Institute of Technology Roorkee is a technical university located in Roorkee, Uttarakhand, India. IIT Roorkee is among the foremost of institutes of national importance in higher technological education and in engineering, basic and applied research. Since its establishment, the Institute has played a vital role in providing the technical manpower and know-how to the country and in pursuit of research."  linked="http://www.iitr.ac.in/ "/>
        <CollegeCards name ="IIT GUWAHTI" bio="Indian Institute of Technology Guwahati is a public technical university established by the Government of India, located in Amingaon area, North Guwahati city, in the state of Assam in India. It is the sixth Indian Institute of Technology established in India." linked="http://www.iitg.ac.in/" />
        <CollegeCards name ="NIT JALANDHAR" bio="Dr. B. R. Ambedkar National Institute of Technology Jalandhar, formerly Regional Engineering College Jalandhar, is a public engineering university located in Jalandhar, Punjab, India. It has been declared as an Institute of National Importance under the Ministry of Human Resource Development, Govt of India." linked="https://www.nitj.ac.in/" />
        </div>

    </>
  )
}

export default Mtech