import React from 'react';
import CollegeCards from '../CollegeCards';

function Mca() {
  return (
    <>
        <div className='btechClgs'>
            <CollegeCards name="GLS UNIVERSITY" bio="GLS University is a statutory state private university established by an Act of Gujarat State Assembly. GLS University is set up with the vision to benchmark global standards of education and to create path breaking programmes in the areas of strategic importance not only to the country but also across the world. The university has initiated various innovative programmes in diverse areas of Management, Information Technology, Communications, Social Sciences, and Commerce. Several programmes such as B.C.A., iMSc (IT), M.C.A, MSc (IT), B.B.A., M.B.A., B.Com., M.Com., etc." linked="https://glsufcait.org/" />
            <CollegeCards name="CHRIST UNIVERSITY" bio="CHRIST, is a deemed to be university in Bangalore, Karnataka, India. Founded in 1969 as Christ College, the University Grants Commission of India conferred autonomy to Christ College in 2004. " linked="http://www.christuniversity.in/" /><CollegeCards name="St. JOSEPH’S UNIVERSITY" bio="St. Joseph's University is a private aided, Jesuit, higher educational institution run by the Karnataka Province of the Society of Jesus in Bangalore, Karnataka, India. It is one of the oldest educational institution in the state of Karnataka with a history of 140 years." linked="https://www.sjc.ac.in/" />
            <CollegeCards name="SYMBIOSIS INSTITUTE OF COMPUTER STUDIES AND RESEARCH" bio="Symbiosis Institute of Computer Studies and Research (SICSR), celebrating its 38th year was one of the first Institutes in Maharashtra to offer high quality computer education. SICSR is located in the heart of Pune City. Our location, our broad portfolio of academic IT programmes, our talented and diverse student body, and our dedicated faculty members work together to create an educational experience that is relevant, enriching, and uniquely symbiotic." linked="https://www.sicsr.ac.in/" />
            <CollegeCards name="GRAPHIC ERA DEEMED TO BE UNIVERSITY" bio="Graphic Era Deemed to be University is a private deemed-to-be university in Clement Town, Dehradun, Uttarakhand, India. " linked="https://www.geu.ac.in/ " />
            <CollegeCards name="AMITY UNIVERSITY" bio="Amity University, Noida is a private research university located in Noida, Delhi NCR, India. It was established in 2005 by an Act of the State Legislature of Uttar Pradesh. The university is recognized by University Grants Commission and accredited by the NAAC with grade 'A+'. " linked="https://www.amity.edu/ " />
            
        </div>
    </>

  )
}

export default Mca