import React from 'react';
import CollegeCards from '../CollegeCards';

function Bpharma() {
  return (
    <>
        <div className='btechClgs'>
            <CollegeCards name="Delhi Institute of Pharmaceutical Sciences and Research" bio="Delhi Institute of Pharmaceutical Sciences and Research is an institution located in New Delhi, India. It is providing teaching and research in pharmaceutical sciences. It is affiliated with Delhi Pharmaceutical Science and Research University." linked="http://www.dipsar.ac.in/" />
            <CollegeCards name="POONA COLLEGE OF PHARMACY" bio="Poona College of Pharmacy was earlier permanently affiliated to University of Pune. It is now a constituent unit of Bharati Vidyapeeth (Deemed to be University). It is a grant-in-aid institute, conducting Graduate, Post-graduate and Doctoral programmes in Pharmaceutical Sciences." linked="http://pcp.bharatividyapeeth.edu/" />
            <CollegeCards name="Sri Ramachandra Institute of Higher Education and Research" bio="Sri Ramachandra Institute of Higher Education and Research, formerly Sri Ramachandra University, formerly Sri Ramachandra Medical College and Research Institute, is a private institute located in Porur, Chennai, India." linked="http://www.sriramachandra.edu.in/ " />
            <CollegeCards name="Maharshi Dayanand University" bio="Maharshi Dayanand University, also called M.D. University or simply MDU, is a university in Rohtak, Haryana, India. Established in 1976 and named after the saint Dayananda Saraswati ." linked="https://mdu.ac.in/" />
            <CollegeCards name="COLLEGE OF PHARMACY" bio="The College of Pharmacy, located at Narhe in the outskirts of Pune, India, operates under the banner of the Abhinav Education Society. The college was established in 2007 and offers a 4-year graduation course in Pharmacy. " linked="https://pcp.bharatividyapeeth.edu/" />
            <CollegeCards name="AISSMS COLLEGE OF PHARMACY" bio="AISSMS College of Pharmacy, Pune is a Pharmacy college affiliated to the AICTE, PCI, New Delhi and University of Pune, India. It was established in 1996. AISSMS COP is among the top Pharmacy colleges in the private sector in Maharashtra." linked="https://www.aissmscop.com/" />
        </div>
    </>
  )
}

export default Bpharma