import React from 'react';
import CollegeCards from '../CollegeCards';

function Bcom() {
  return (
    <>
        <div className='btechClgs' >
            <CollegeCards name="HANS RAJ COLLEGE" bio="Established in 1954, Hans Raj College is located at Delhi University’s North Campus. The college offers arts, science and commerce courses to aspirants at the UG as well as PG level. Apart from these courses, Hans Raj College also offers certificate and diploma programmes in languages to aspirants. One of the renowned colleges in the national capital, Hans Raj College is known for its academic excellence in the field of Commerce. Many renowned aluminas have graduated from the established college which is as follows. " linked="http://www.hansrajcollege.ac.in/" />
            <CollegeCards name="HINDU COLLEGE" bio="Hindu college is ranked among the top colleges in the country, including NIRF Colleges Rankings. Students aspire to get admission in Hindu College because of its highly qualified faculty members, and the teaching learning process which has mix of theory and practical. Hindu College, another prominent college of Delhi University is also located in the north campus of DU. Established in 1899, is also one of the oldest constituent colleges of Delhi University. The college offers commerce, arts and science courses at the undergraduate (UG) as well as postgraduate (PG) level. Apart from these degree courses, Hindu College also offers certificate programmes in languages to aspirants." linked="https://hinducollege.ac.in/" />
            <CollegeCards name="GARGI COLLEGE" bio="Established in 1967, Gargi College is a part of DU’s South Campus. The college offers UG courses in commerce, arts and science streams whereas PG level courses are offered only in arts and science stream." linked="https://gargicollege.in/" />
            <CollegeCards name="UNIVERSITY OF MUMBAI" bio="The University of Mumbai is a collegiate, state-owned, public research university in Mumbai. The University of Mumbai is one of the largest universities in the world. As of 2013, the university had 711 affiliated colleges. Ratan Tata is the appointed head of the advisory council." linked="https://mu.ac.in/" />
            <CollegeCards name="LOYOLA COLLEGE" bio="Loyola College is a private Catholic higher education institution run by the Society of Jesus in Chennai, Tamil Nadu, India. It was founded in 1925 by the French Jesuit priest, Francis Bertram, along with other European Jesuits. It is an autonomous Jesuit college affiliated with the University of Madras." linked="https://www.loyolacollege.edu/" />
            <CollegeCards name="SRI VENKATESWARA COLLEGE" bio="Established in 1961, Sri Venkateswara College is one of the most sought after colleges for B.Com in south campus of Delhi University. The college offers UG courses in commerce, arts and science streams whereas PG level courses are offered only in arts and science stream. Apart from these courses, the college also offers professional/vocational courses to aspirants." linked="https://www.svc.ac.in/" />
        </div>
    </>
  )
}

export default Bcom